import React, {Component} from 'react';

class About extends Component {
    render() {
        return (
            <React.Fragment>
                <section id="about">
                    <div className="row section-intro">
                        <div className="col-twelve">
                            <h5>About</h5>
                            <h1>Let me introduce myself.</h1>
                            <div className="intro-info">
                                <img src="images/profilepic.png" alt="Profile Picture" />
                                <p className="lead">
                                    I'm a simple guy. I enjoy playing video games, and I <i>really</i> enjoy making them. Creating art, for me,
                                    is much more than just painting or sculpting or writing music. Instead, my creativity lies within the
                                    act of combining these many aspects of art to bring entirely new worlds into existence. New stories, new mysteries,
                                    new sights and sounds - to me, there is <i>nothing</i> more satisfying than piquing the curiosity of another individual.
                                </p>
                            </div>
                        </div>
                    </div> {/* /section-intro */}
                    <div className="row about-content">
                        <div className="col-six tab-full">
                            <h3>Profile</h3>

                            <ul className="info-list">
                                <li>
                                    <strong>Full Name:</strong>
                                    <span>Shane Michael Caler</span>
                                </li>
                                <li>
                                    <strong>Birth Date:</strong>
                                    <span>Feburary 22, 1994</span>
                                </li>
                                <li>
                                    <strong>Position:</strong>
                                    <span>Student, Master of Arts: Game Development</span>
                                </li>
                                <li>
                                    <strong>Github:</strong>
                                    <span>https://github.com/ShaneCaler/</span>
                                </li>
                                <li>
                                    <strong>Email:</strong>
                                    <span>caler.shane@gmail.com</span>
                                </li>
                            </ul> {/* /info-list */}
                        </div>
                        <div className="col-six tab-full">
                            <h3>Skills</h3>
                            <p>Primarily, my skills lie in working with Unity 3D and the C# language, though I do have varying experience with the latter languages.
                            </p>
                            <ul className="skill-bars">
                                <li>
                                    <div className="progress percent90"><span>90%</span></div>
                                    <strong>C#</strong>
                                </li>
                                <li>
                                    <div className="progress percent90"><span>90%</span></div>
                                    <strong>Unity3D</strong>
                                </li>
                                <li>
                                    <div className="progress percent75"><span>75%</span></div>
                                    <strong>HTML</strong>
                                </li>
                                <li>
                                    <div className="progress percent70"><span>70%</span></div>
                                    <strong>Adobe Photoshop</strong>
                                </li>
                                <li>
                                    <div className="progress percent60"><span>60%</span></div>
                                    <strong>JavaScript</strong>
                                </li>
                                <li>
                                    <div className="progress percent60"><span>60%</span></div>
                                    <strong>Python</strong>
                                </li>


                            </ul> {/* /skill-bars */}
                        </div>
                    </div>
                    <div className="row button-section">
                        <div className="col-twelve">
                            <a href="#contact" title="Hire Me" className="button stroke smoothscroll">Hire Me</a>
                            {/* <a href="#" title="Download CV" className="button button-primary">Download CV</a> */}
                        </div>
                    </div>
                </section> {/* /process*/}
            </React.Fragment>
        );
    }
}

export default About;