import React, {Component} from 'react';

class Introduction extends Component {
    render() {
        return (
            <React.Fragment>
                <section id="intro">
                    <div className="intro-overlay" />
                    <div className="intro-content">
                        <div className="row">
                            <div className="col-twelve">
                                <h5>Hi there,</h5>
                                <h1>I'm Shane Caler.</h1>
                                <p className="intro-position">
                                    <span>Video Game Developer</span>
                                </p>
                                <a className="button stroke smoothscroll" href="#about" title>Get To Know Me</a>
                            </div>
                        </div>
                    </div> {/* /intro-content */}
                    <ul className="intro-social">
                        <li><a href="https://github.com/ShaneCaler/" target="_blank"
                               rel="noopener noreferrer"><i className="fa fa-github-square" /></a></li>
                        <li><a href="https://www.linkedin.com/in/shanecaler/" target="_blank"
                               rel="noopener noreferrer"><i className="fa fa-linkedin-square" /></a></li>
                    </ul> {/* /intro-social */}
                </section> {/* /intro */}
            </React.Fragment>
        );
    }
}

export default Introduction;