import React, {Component} from 'react';
import Modal from './modal';
import Grid from '@material-ui/core/Grid';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import portfolioData from '../../portfolioData';

const styles = theme => ({
    root: {
        flexGrow: 1,
    },

});

function AutoGrid(props) {
    const {classes} = props;

        return (
            <React.Fragment>
                    <section id="portfolio">
                        <div className="row section-intro">
                            <div className="col-twelve">
                                <h5>Portfolio</h5>
                                <h1>Check Out Some of My Works.</h1>
                                <p className="lead">I look forward to expanding upon this section with projects from a job. Until then,
                                    I plan to continue completing personal projects in an attempt to learn as much as I possibly can.</p>
                            </div>
                        </div> {/* /section-intro*/}

                        <div className={classes.root}>

                            <Grid container
                                  alignItems="stretch"
                                  alignContent="center"
                                  justify="center"
                            >
                                <Grid item xs={5} >
                                    <Modal  title={"Tower Defense Prototype"} subTitle = {"Game Design"}
                                            description = {portfolioData.towerDefenseDesc} image = {"images/portfolio/towerdefenseimg.png"} link={"https://github.com/ShaneCaler/tower-defense"}/>
                                </Grid>
                                <Grid item xs={5} >
                                    <Modal title={"Flappy Bird/Snake AI"} subTitle = {"Machine Learning"}
                                           description = {portfolioData.mlDesc} image = {"images/portfolio/machinelearningdemosimg.png"} link={"https://github.com/ShaneCaler/machine-learning-demos"}/>
                                </Grid>
                                <Grid item xs={5} >
                                    <Modal title={"Combat-oriented Go-Kart Racing Game"} subTitle = {"Virtual Reality"}
                                           description = {portfolioData.kartDesc} image = {"images/portfolio/vrgameimg.png"} link={"https://github.com/ShaneCaler/VR_Racing_Game"}/>
                                </Grid>
                            </Grid>
                        </div>

                    </section> {/* /portfolio */}
            </React.Fragment>
        );
    }


AutoGrid.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(AutoGrid);