import React, {Component} from 'react';
import axios from 'axios';

class Contact extends Component {
    state = {
        name: '',
        message: '',
        email: '',
        sent: false,
        buttonText: 'Send Message'
    };

    formSubmit = (e) => {
        e.preventDefault()

        this.setState({
            buttonText: '...sending'
        });

        let data = {
            name: this.state.name,
            email: this.state.email,
            message: this.state.message
        };

        axios.post('API_URI', data)
            .then( res => {
                this.setState({ sent: true }, this.resetForm())
            })
            .catch( () => {
                console.log('Message not sent')
            })
    };

    resetForm = () => {
        this.setState({
            name: '',
            message: '',
            email: '',
            buttonText: 'Message Sent'
        })
    };

    render() {
        return (
            <React.Fragment>
                <section id="contact">
                    <div className="row section-intro">
                        <div className="col-twelve">
                            <h5>Contact</h5>
                            <h1>I'd Love To Hear From You.</h1>
                            <p className="lead">I am currently looking for work or an internship while I attend graduate school, so
                                I am more than interested to hear any offers you may have.
                            </p>
                        </div>
                    </div>
{/*
                    <div className="row contact-form">
                        <div className="col-twelve">
                             form
                            <form name="contactForm" id="contactForm" onSubmit={ (e) => this.formSubmit(e)}>
                                <fieldset>
                                    <div className="form-field">
                                        <input name="contactName" type="text" id="contactName"
                                               placeholder="Name" onChange={e => this.setState({ name: e.target.value})}
                                               defaultValue={""} minLength={2} required
                                               value={this.state.name}/>
                                    </div>
                                    <div className="form-field">
                                        <input name="contactEmail" type="email" id="contactEmail"
                                               placeholder="Email" onChange={(e) => this.setState({ email: e.target.value})}
                                               defaultValue={""} required
                                               value={this.state.email}/>
                                    </div>
                                    <div className="form-field">
                                        <input name="contactSubject" type="text" id="contactSubject"
                                               onChange={(e) => this.setState({ subject: e.target.value})}
                                               placeholder="Subject" defaultValue={""}
                                               value={this.state.subject}/>
                                    </div>
                                    <div className="form-field">
                                        <textarea name="contactMessage" id="contactMessage"
                                                  placeholder="Message" onChange={e => this.setState({ message: e.target.value})}
                                                  rows={10} cols={50} defaultValue={""} required/>
                                    </div>
                                    <div className="form-field">
                                        <button type="submit" className="submitform">{this.state.buttonText}</button>
                                        <div id="submit-loader">
                                            <div className="text-loader">Sending...</div>
                                            <div className="s-loader">
                                                <div className="bounce1"/>
                                                <div className="bounce2"/>
                                                <div className="bounce3"/>
                                            </div>
                                        </div>
                                    </div>
                                </fieldset>
                            </form>
                             Form End
                             contact-warning
                            <div id="message-warning">
                            </div>
                             contact-success
                            <div id="message-success">
                                <i className="fa fa-check"/>Your message was sent, thank you!<br/>
                            </div>
                        </div>
                         /col-twelve
                    </div>*/}
                    {/* /contact-form */}
                    <div className="row contact-info">
                        <div className="col-four tab-full">
                            <div className="icon">
                                <i className="icon-pin"/>
                            </div>
                            <h5>Where to find me</h5>
                            <p>
                                Castro Valley, CA<br/>
                                94546 US
                            </p>
                        </div>
                        <div className="col-four tab-full collapse">
                            <div className="icon">
                                <i className="icon-mail"/>
                            </div>
                            <h5>Email Me At</h5>
                            <p>caler.shane@gmail.com<br/>
                            </p>
                        </div>
                        <div className="col-four tab-full">
                            <div className="icon">
                                <i className="icon-phone"/>
                            </div>
                            <h5>Call Me At</h5>
                            <p>Phone: (707)-474-7976<br/>
                            </p>
                        </div>
                    </div>
                    {/* /contact-info */}
                </section>
                {/* /contact */}
            </React.Fragment>
        );
    }
}

export default Contact;